import React from 'react';
import {App, Result} from 'antd';
import {Link} from "react-router-dom";
import cl from "./style.module.css";
const Page404 = () => {
    document.title = "404 -  "
    return (
        <App>
            <Result
                status="404"
                title="404"
                subTitle=", ,   ,  ."
                extra={<Link className={cl.button} to={'/'}> </Link>}
            />
        </App>
    );
};

export default Page404;